@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";


* {
    font-family: 'Montserrat';
    font-weight: 400;
    font-style: normal;
    color: $text-main-color;
    text-decoration: none;
}
.container {
    width: 100%;
    max-width: 1560px;
    margin: 0 auto;
    padding: 0 10px;    
}
.btn {
    padding: 0;
    font-family: inherit;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    outline: none;
    width: 100%;
    max-width: 360px;
    padding: 20px;
      padding-bottom: 20px;
    padding-bottom: 21px;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.21;
    text-transform: uppercase;
    text-align: center;
    color: #ffffff;
    background-color: #000000;
    border: 1px solid #000000;
    -webkit-transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
    transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
  }
.ordering {
    background-color: #f9f9f9;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        box-sizing: border-box;
    }
}
.ordering__form-wrap {
    max-width: 740px;
    width: 100%;
    margin-top: 100px;
}
.ordering__form-wrap__container {
    &:not(:last-child) {
        margin-bottom: 30px;
    }
    .cost{
        display: block;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        text-transform: uppercase;
        
        color: #000000;        
    }
    .ordering__form-wrap__radio {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
    }
    .ordering__form-wrap__radio:checked+.custom-radio::after {
        opacity: 1;
    }

    .ordering__form-wrap__check {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        &:checked+.custom-check {
            &::after {
                opacity: 1;
            }
        }
    }
    .custom-check {
        margin-right: 10px;
        position: relative;
        width: 12px;
        height: 12px;
        box-sizing: border-box;
        border: 1px solid #000000;
        
        display: flex;
        justify-content: center;
        align-items: center;
        
        &::after {
            opacity: 0;
            content: '';
            width: 12px;
            height: 12px;
            box-sizing: border-box;
            background-image: url('../img/check.svg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            display: block;
        }
    }

    .custom-radio {
        margin-right: 13px;
        border: 1px solid #000000;
        box-sizing: border-box;  
        width: 17px;
        height: 17px;
        padding: 3px;
        border-radius: 50%;

        display: flex;
        justify-content: center;
        align-items: center;

        &::after {
            opacity: 0;
            content: '';
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            background-color: #000;
            display: block;
            border-radius: 50%;
        }
    }
    
    .ordering__form-wrap__container__header {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000;  
        
        margin-bottom: 29px;
    }

    .ordering__form-wrap__text-input {
        box-sizing: border-box;
        width: 100%;
        border: 1px solid #999999;
        outline: none;
        padding: 15px 20px 15px;

        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;  
        color: #000;      
    }

    &.shipment-type {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        flex-direction: column;
        label:not(:last-child) {
            margin-bottom: 23px;
        }
    }

    &.payment-type {
        .ordering__form-wrap__label {
            display: inline-flex;
            &:not(:last-child) {
                margin-right: 44px;
            }
        }
    }

    ._small {
        font-size: 11px !important;
        line-height: 13px !important;  
        margin: 10px 0 0 10px;
    }

    .ordering__form-wrap__check {
        margin-right: 10px;
    }

    .ordering__form-wrap__label {
        display: flex;

        cursor: pointer;
        position: relative;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000;        
    }

    .ordering__form-wrap__btn {
        margin-bottom: 20px;
        display: block;
    }
}
.ordering__form-wrap__submit-container {
    display: flex;
    justify-content: start;
    align-items: flex-start;
    flex-direction: column;

    span {
        margin-top: 10px;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 150%;
        
        /* or 15px */
        
        color: #000000;
        max-width: 380px;        
    }
    a {
        color: #000000;
    }
}

.ordering__info-wrap {
    max-width: 550px;
    padding: 50px 30px 93px;
    background: #FFFFFF;
    box-sizing: border-box;
    margin-top: 100px;

    span, .ordering__info-wrap__text {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 170%;
        
        /* or 24px */
        text-transform: uppercase;
        
        color: #707070;        
    }
    a {
        display: block;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #333333;
        margin: 21px 0 21px;        
    }
    img {
        &:not(:last-child) {
            margin-right: 20px;
        }
    }
}

.shipment-type__content {
    .ordering__form-wrap__text-input {
        margin-bottom: 30px;
    }
    .shipment-type__content__group {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .ordering__form-wrap__label {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px; 
        margin-bottom: 10px !important;       
    }
    .shipment-type__content__label-wrap {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
        span {
            display: block;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            
            /* identical to box height */
            text-transform: uppercase;
            
            color: #000000;            
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
}

.ordering__form-wrap__container__point {
    span {
        display: block;
    }
    .ordering__form-wrap__container__point__main-head {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000; 
        margin-bottom: 30px;       
    }
    .ordering__form-wrap__container__point__head {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000;
        margin-bottom: 10px;
    }
    .ordering__form-wrap__container__text {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000; 
        margin-bottom: 21px;       
    }
    .ordering__form-wrap__container__point__link {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #333333; 
        text-decoration: underline;       
    }
}

.pickup-popup {
    max-width: 1400px;
    max-height: 800px;
    width: 100% !important;
    height: 100%;
    // padding: 50px 30px;
    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: stretch;

    .pickup-popup__wrapper {
        max-width: 290px;
        width: 100%;
        overflow-y: auto;
    }
    .pickup-popup__header {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000;   
        margin-bottom: 10px; 
    }
    .pickup-popup__list {
        height: max-content;
    }
    .pickup-popup__list__item {
        border-bottom: 1px solid #999999;
        padding-bottom: 20px;
        margin-right: 10px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    .pickup-popup__list__item__header {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000; 
        margin-bottom: 10px;       
    }
    .pickup-popup__list__item__text {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        
        color: #000000; 
        margin-bottom: 15px;       
    }
    .pickup-popup__list__item__desc {
        margin-top: 15px;
        display: block;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        
        /* identical to box height */
        text-transform: uppercase;
        
        color: #000000;        
    }
    .pickup-popup__map {
        width: 100px;
        height: 500px;
    }
}

.shipment-type__time-box__wrap {
    display: flex;
    justify-content: space-between;
}

.custom-select {
    position: relative;
    max-width: 360px;
    width: 100%;
    // padding: 30px 0;    
}
.custom-select__placeholder {
    left: 20px;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    
    /* identical to box height */
    text-transform: uppercase;
    
    color: #000000;

    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    -webkit-transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
    transition: opacity 0.3s ease, -webkit-transform 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
    pointer-events: none;    
}
.custom-select__button {
    height: 50px;
    width: 100%;
    margin: 0;
    padding: 17px 20px;

    background: #F9F9F9;
    border: 1px solid #999999;
    box-sizing: border-box;

    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 0;
}
.custom-select__list {
    position: absolute;
    bottom: 1px;
    z-index: 1;
    width: 100%;
    max-height: 100px;
    margin: 0;
    padding: 17px 20px;
    box-sizing: border-box;
    overflow-y: auto;
    list-style: none;
    background-color: #ffffff;
    border: 2px solid #000000;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    visibility: hidden;
    opacity: 0;
    -webkit-transition: visibility 0.3s ease, opacity 0.3s ease;
    transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select select {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);    
}
.custom-select.is-open .custom-select__list {
    visibility: visible;
    opacity: 1;
  }
  .ordering__form-select .custom-select__list {
    // bottom: 7px;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }

  .custom-select__item {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    
    /* identical to box height */
    text-transform: uppercase;
    
    color: #000000;
      &:not(:last-child) {
          margin-bottom: 10px;
      }
  }
  .custom-select.not-empty .custom-select__placeholder {
    opacity: 0;
  }
  .custom-select__text {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    
    /* identical to box height */
    text-transform: uppercase;
    
    color: #000000;
      text-align: left;
      display: block;
  }